<template>
  <v-dialog v-model="isOpen" max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" dark v-bind="attrs" v-on="on">
        Увійти
      </v-btn>
    </template>
    <v-card class="py-16">
      <v-card-title class="justify-center pt-10">
        <span class="text-h2 font-weight-bold grey--text text--darken-2"
          >Увійти</span
        >
      </v-card-title>
      <v-card-text class="pa-10">
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field
                label="Пошта"
                v-model="email"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field
                v-model="password"
                label="Пароль"
                required
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show ? 'text' : 'password'"
                @click:append="show = !show"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions class="justify-space-around pb-5">
        <ApolloQuery
          :query="$options.queries.loginQuery"
          :variables="{
            cred: {
              email: this.email,
              password: this.password
            }
          }"
          :prefetch="false"
          v-slot="{ query, result: { error, data } }"
          ref="loginQuery"
        >

          <!-- Error -->
          <Snackbar
            v-if="error"
            :is-open="$store.getters.getSnackbarState.isOpen"
            >Логін або пароль невірні</Snackbar
          >

          <!-- Result -->
          <Snackbar
            v-else-if="data"
            :is-open="$store.getters.getSnackbarState.isOpen"
          >
            Авторизовано
          </Snackbar>
          <v-btn color="blue white--text px-16" large @click="query.refetch(); $store.dispatch('toggleSnackbar', true) ">
            Увійти
          </v-btn>
        </ApolloQuery>
        <v-btn color="blue white--text px-11" large @click="isOpen = false">
          Зареєструватися
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { ApolloQuery } from 'vue-apollo'
import loginQuery from '@/graphql/queries/login.gql'
import Snackbar from '../SnackBar.vue'
export default {
  components: {
    ApolloQuery,
    Snackbar
  },
  queries: {
    loginQuery
  },
  data: () => ({
    isOpen: false,
    show: false,
    email: '',
    password: ''
  })
}
</script>

<style></style>
