<script>
import { mapGetters } from 'vuex'
import Post from '@/components/posts/Post'

export default {
  components: {
    Post
  },
  computed: {
    ...mapGetters({
      categories: 'getCategories',
      posts: 'getPosts'
    })
  },
  async mounted () {
    await this.$store.dispatch('addCategories')
    await this.$store.dispatch('addPosts')
  }
}
</script>

