import Vue from 'vue'
import Vuex from 'vuex'
import { apolloClient } from '@/graphql/apollo'
import postsQuery from '@/graphql/queries/posts.gql'
import categoriesQuery from '@/graphql/queries/categories.gql'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    snackbarIsOpen: false,
    posts: [],
    categories: []
  },
  mutations: {
    setSnackbarState (state, isOpen) {
      state.snackbarIsOpen = isOpen
    },
    setPosts: (state, payload) => {
      state.posts = payload
    },
    setCategories: (state, payload) => {
      state.categories = payload
    }
  },
  getters: {
    getSnackbarState: state => state.snackbarIsOpen,
    getPosts: state => state.posts,
    getCategories: state => state.categories
  },
  actions: {
    toggleSnackbar ({ commit }, isOpen) {
      commit('setSnackbarState', isOpen)
    },
    addCategories: async ({ commit, state }) => {
      try {
        if (state.categories.length) {
          return
        }
        const { data } = await apolloClient.query({
          query: categoriesQuery
        })
        if (data?.getCategories) {
          commit('setCategories', data.getCategories)
        }
      } catch (e) {
        throw new Error(e)
      }
    },
    addPosts: async ({ commit, state }, pagination = { page_num: 0, limit: 20, type: 'REQ' }) => {
      try {
        if (state.posts.length) {
          return
        }
        const { data } = await apolloClient.query({
          query: postsQuery,
          variables: {
            pagination
          }
        })
        console.log(data)
        if (data?.listPosts?.posts) {
          commit('setPosts', data?.listPosts?.posts)
        }
      } catch (e) {
        throw new Error(e)
      }
    }
  }
})
